<template>
    <div id="popup">
        <form action="#" id="form">

            <div id = "search_bar">
                <div id="search">
                    <label for="key"><i class="iconfont icon-sousuo"></i></label><input id="key" type="text" v-model.lazy.trim="keyValue">
                </div>                       
                <button type="button" @click="isIndexDate=!isIndexDate" title="日期检索"><i class="iconfont icon-riqi1"></i></button>
                <button type="button" @click="isIndexTag=!isIndexTag" title="标签检索"><i class="iconfont icon-biaoqian1"></i></button>
            </div> 
            
            <div v-show="isIndexDate" id="date_bar">
                <label for="start">起始</label><input type="date" id="start" v-model="dateStart">
                <label for="end">结束</label><input type="date" id="end" v-model="dateEnd">
            </div>

            <div v-show="isIndexTag" id="tag_bar">
                <select name="" id="tagselect" v-model="selectTag">
                    <option value="">请选择一个标签，进行筛选</option>
                    <option v-for="(item,index) of clipTags" :key="index" v-bind:value="item">{{item}}</option>
                </select>
            </div> 
          
        </form>

        <ul>
            <div>
                <span class="index">No.</span>
                <span class="content">内容</span>
                <span class="tag">标签</span>
                <span class="function">操作</span>
            </div>

            <li class="itemshow" v-for="(item,index) in showBoard" :key="item.id">
                <span class="index">{{index+1}}</span>
                <span class="content" :title="item.content">{{item.content}}</span>
                <span class="tag" @click="showTagPage(item)">{{item.tag=="default"?"默认":item.tag}}</span>
                <span class="function">
                    <button type="button" id="delete" @click="copyItem(item)" title="复制"><i class="iconfont icon-fuzhi"></i></button>
                    <button type="button" id="delete" @click="editItem(item)" title="编辑"><i class="iconfont icon-bianji"></i></button>
                    <button type="button" id="delete" @click="deleteItem($event,item)" title="删除"><i class="iconfont icon-shanchu1"></i></button>
                </span>
            </li>

        </ul>

        <Tag v-if="tagEditPage" :tags="clipTags" :item = "itemForTagPage"></Tag>

    </div>            
</template>

<script>
    import Tag from "./Tag.vue";
    export default {
        name: "PopUp",
        components: {
            Tag,
        },
        data(){
            return{
                clipBoard:"",
                clipTags:["default","1sfsd","2dfsf","3fafas","default","1sfsd","2dfsf","3fafas","default","1sfsd","2dfsf","3fafas"],
                keyValue:"",
                selectTag:"",
                dateStart:this.getToday(),
                dateEnd:this.getToday(),
                isIndexDate:false,
                isIndexTag:false,
                tagEditPage:false,
                itemForTagPage:null
            }
        },
        computed: {
            showBoard(){
                if(this.clipBoard=="")return[{id:1,content:"asafdafs",tag:"default"},{id:2,content:"asafdafs",tag:"default"},{id:3,content:"asafdafs",tag:"default"}];
                /**
                 * [{id:1,content:"asafdafs",tag:"default"},{id:2,content:"asafdafs",tag:"default"},{id:3,content:"asafdafs",tag:"default"}]
                 */               
                let arr = this.clipBoard.slice(1,this.clipBoard.length).filter((item)=>{
                   return item.content.indexOf(this.keyValue) !== -1;
                })
                if(this.isIndexDate){
                    let checkTime1 = Date.parse(this.dateStart)+new Date().getTimezoneOffset()*60*1000;
                    let checkTime2 = Date.parse(this.dateEnd)+new Date().getTimezoneOffset()*60*1000+24*60*60*1000;
                    arr = arr.filter((item)=>{
                        return item.id >= checkTime1 && item.id < checkTime2;
                    })    
                }
                if(this.isIndexTag&&this.selectTag){
                    arr = arr.filter((item)=>{
                        return item.tag == this.selectTag;
                    })    
                }
                return arr.reverse();; 
            }
        },
        methods: {
            //初始化
            getToday(){
                let date = new Date();
                let year = date.getFullYear();
                let month = date.getMonth()>8?date.getMonth()+1:"0"+(date.getMonth()+1);
                let day = date.getDate()>9?date.getDate():"0"+date.getDate();
                return year + "-" + month + "-" + day; 
            },
            //页面 列表单项操作
            deleteItem(event,item){
                this.clipBoard.splice(this.clipBoard.indexOf(item),1);
                this.setClipBoard();
            },
            copyItem(item){
                let copyText = item.content;
                navigator.clipboard.writeText(copyText).then();
            },
            editItem(item){
                console.log("debug 00 开始修改");
                const oldText = item.content;
                const newText = prompt("",`${oldText}`);
                if(newText!==oldText){                    
                    let newItem = {id:item.id,content:newText,tag:item.tag};
                    this.setItem(item,newItem);                    
                }
            },
            //与后台交互
            async getClipBoard(){
                this.clipBoard = await new Promise((resovle)=>{
                chrome.storage.local.get(['clipData'], function(result){resovle(result.clipData)})
                })              
            },
            async getTags(){
                console.log("debug 07.5 开始刷新页面");
                this.clipTags = await new Promise((resovle)=>{
                chrome.storage.local.get(['tags'], function(result){resovle(result.tags)})
                })
                console.log("debug 08 刷新页面");
            },
            setClipBoard(){              
                chrome.storage.local.set({clipData: this.clipBoard}, function(){}) 
            },
            setItem(oldItem,newItem){
                console.log("debug 01 调用setItem函数")
                chrome.runtime.sendMessage(["edit item",oldItem,newItem], response => {
                    console.log("debug 07 页面收到报告，开始刷新页面");
                    if(response==="edit content success"){
                        this.getClipBoard();
                    }
                })
            },
            setTags(){
                chrome.storage.local.set({clipData: this.clipTags}, function(){}) 
            },
            //标签页相关
            showTagPage(item){
                this.itemForTagPage = item;
                this.tagEditPage = true;
                this.$bus.$on("tagPageCallback",this.tagPageCallback)
            },
            tagPageCallback(data){
                this.tagEditPage = false;
                if(data.edited&&data.oldItem.tag!==data.newItem.tag)this.setItem(data.oldItem,data.newItem);
                this.$bus.$off("tagPageCallback");
            }
        },
        created() {
            this.getClipBoard();
            this.getTags();   
        }
    }  
</script>

<style lang="less" scoped>
    @zimablue:rgb(91,194,231);
    i {
        vertical-align: baseline;
    }
    #popup {
        width:400px;
        border: 1px solid black;
        position: relative;   
        #form {
            margin:0px auto;
            width:396px;            
            display: flex;
            flex-flow: column wrap;
            top: 0px;
            transition: height 1.5s ease;
            background-color: snow;
            #search_bar {
                height: 30px;
                display: flex;
                flex-flow: row nowrap;
                align-items: center;
                padding: 0px 2px;
                font-weight: 700;
                background-color: @zimablue;
                border-radius: 5px;
                #search {
                    flex:10;
                    height: 24px;
                    background-color: antiquewhite;
                    border-radius: 5px;
                    display: flex;
                    align-items: center;
                    overflow: hidden;
                    label {
                        padding: 0px 4px;
                        i {color: navy;}
                    }
                    input {
                        outline:0px;
                        border: 0px;
                        background-color: inherit;
                        flex: 1;
                    }
                }
                button {
                    font-weight: inherit;
                    flex:1;
                    height:24px;
                    background-color:inherit;
                    border: 0px;
                    border-radius: 5px;
                    cursor: pointer;
                    i {color: snow;};
                    &:active {transform: translateY(3px)};
                }
            }
            #date_bar {
                margin: 0px auto;
                width:100%;
                display: flex;
                justify-content: center;
                align-items: center;
                color: @zimablue;
                font-weight: 700;
                input {
                    background-color: antiquewhite;
                    color: darkgrey;
                    border: 0px;
                    outline: 0px;
                    margin: 3px 0px;
                    border-radius: 5px;
                }
                label {
                    margin: 0px 5px;
                }
            }
            #tag_bar {
                margin: 0px auto;
                width:100%;
                display: flex;
                justify-content: center;
                align-items: center;
                select {
                    margin: 3px;
                    border-radius: 5px;
                    background-color: antiquewhite;
                    border: 0px;
                    outline: 0px;
                    color: darkgrey;
                    text-align: center;
                }
            }
            hr {
                width: 100%;
                height: 1px;
                border:0px;
                background-color: snow;
            }
        }
        ul {
            width:100%;
            height:500px; 
            list-style: none;
            overflow-y: auto;          
            li {
                .items(18px,4,1,1);
                .index {  
                    &[selected] {
                        background-color: navy;
                        color: antiquewhite;
                    }
                    border:2px solid navy;;                
                    border-radius: 100%;                   
                    text-align:center;
                    margin: 0px 3px;
                    line-height: 14px;
                    font-size: 12px;
                }
                .tag {
                    text-align: center;
                    color: navy;
                }
                .function {
                    display: flex;
                    align-items: center;
                    justify-content: space-around;                    
                    button {
                        background-color: inherit;
                        border: 0px;
                        cursor: pointer;
                        i {color: peachpuff;};
                        &:hover i {color: hotpink};
                        &:active i {color: red;zoom: 1.2};
                    }
                    
                }
            }
            &>div {
                .items(24px,4,1,1);
                text-align: center;
                background-color: azure;
                color: #222;
            }
        }
        .items(@index,@content,@tag,@function){
            margin:0px 2px;
            height: 20px;
            line-height: 20px;
            display: flex;
            align-items: center;
            justify-content: stretch;
            overflow: hidden;
            .index {
                flex:0 0 @index;
            }
            .content {
                flex: @content;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            .tag {
                flex: @tag;    
            }
            .function {
                flex: @function;
                border: 0px;
            }

        }

    } 
</style>