<template>
      <div id="cap">
          <div id="editPage">
                <h2 id="title">修改标签</h2>
                <span id="tagChangeShow">{{item.tag}}<i class="iconfont icon-forward"></i>{{current.tag}}</span>
                <ul id="tagsShow">
                    <li v-for="(tag,index) in tags" :key="index" @click="changeTag(tag)">{{tag}}</li>
                </ul>
                <span id="controlBar">
                    <button @click="addTag"><i class="iconfont icon-plus-square">新增</i></button>
                    <button @click="deleteTag"><i class="iconfont icon-minus-square">删除</i></button>
                </span>
                <span id="confirmBar">
                    <button @click="confirm"><i class="iconfont icon-circle-check">确认</i></button>
                    <button @click="cancel"><i class="iconfont icon-circle-close">取消</i></button>
                </span>  
          </div>
      </div>
</template>
    
<script>
export default {
    name:"Tag",
    props:{
        tags: {
            default:["default"],
        },
        item:{
            default:{id:0,tag:"default",content:""}
        },
        },
    data(){
        return {
            current:{...this.item},
        } ;
    },
    methods: {
        changeTag(tag){
            this.current.tag = tag;
        },
        confirm(){
            const data = {edited:true,oldItem:this.item,newItem:this.current};
            this.$bus.$emit("tagPageCallback",data);
        },
        cancel(){
            const data = {edited:false};
            this.$bus.$emit("tagPageCallback",data);
        }
    },
}
</script>

<style lang="less" scoped>
    @zimablue:rgb(91,194,231);    
    #cap {
        width: 100%;
        height: 100%;
        background-color: rgba(91,194,231,0.5);
        position: absolute;
        overflow: hidden;
        top: 0px;
        #editPage {
            margin: auto;
            padding: 20px;
            overflow: hidden;
            width: 250px;
            height: 250px;
            position: absolute;
            top: 0;right: 0;bottom: 150px;left: 0;
            background-color: aliceblue;
            display: flex;
            flex-direction: column;
            justify-content: stretch;
            align-items: center;
            #title {
               flex: 0 0 30px;
               line-height: 30px; 
               background-color: @zimablue;
               width: 100%;
               color: #eee;
               text-align: center;
               vertical-align: middle;
            }
            #tagChangeShow {
                flex: 1;
                width: 200px;
                display: flex;
                justify-content: space-around;
                align-items: center;
                i {
                    color: hotpink;
                    margin:0px 20px;
                }               
            }
            #tagsShow {
                flex: 4;
                overflow-y:auto;
                display: flex;
                flex-flow: row wrap;
                width: 100%;
                justify-content: space-between;
                align-items: center;
                align-content: flex-start;
                li {
                    list-style-type:none;
                    height: 30px;
                    line-height: 18px;
                    background-color: lemonchiffon;
                    border: 1px solid navy;
                    border-radius: 10px;
                    min-width: 30px;
                    padding: 5px;
                    text-align: center;
                    &:hover {
                        background-color: hotpink;
                        color: #ddd;
                        zoom: 1.1;
                    }
                    &:active {
                        transform: translate(-2px,2px);
                    }
                }        
            }
            #controlBar {
                flex: 1;
                display: flex;
                width: 100%;
                justify-content: flex-start;
                button {
                    background-color: inherit;
                    border: none;
                    i {
                        color: coral;
                        zoom: 0.9;
                        vertical-align: baseline;
                    }
                }
            }
            #confirmBar {
                flex: 1;
                width: 100%;
                display: flex;
                justify-content: space-around;
                button {
                    background-color: pink;
                    border: 2px solid navy;
                    width: 90px;
                    padding:0px 5px;
                    border-radius: 10px;
                    i {
                        color: navy;
                        font-size: 20px;
                    }
                    &:hover {
                        background-color: hotpink;
                        i {color: #eee;}
                    }
                }
            }

        }
    }
</style>